#dp-information{
    width: 100%;
    height: auto;
    background-color: map-get($color, text2 );
    transition: .3s ease-in-out;
    overflow: hidden;
    .information{
        @include section(s70, padding-bottom);
        @include section(s70, padding-top);
        &__wrapper{
            width: 100%;
            &--title{
                color: map-get($color, bg );
            }
            &--list{
                @include section(s70, padding-top);
                display: flex;
                flex-wrap: wrap;
                margin: 0 -8px;
                // @include rp(tx){
                //     margin: 0 ;
                // }
                &_items{
                    padding: 8px;
                    
                    .items{
                        background-color: white;
                        height: 100%;
                        padding: 20px 30px;
                        transition: .3s ease-in-out;
                        cursor: pointer;
                        min-height: 460px;
                        display: flex;
                        flex-direction: column;
                        @include rp(mx){
                            min-height: 350px;
                            padding: 10px;
                        }
                        &>*{
                            flex: 1 2;
                            transition: .2s ease-in-out;
                            color: map-get($color, text1 );
                        }
                        &-name{
                            position: relative;
                            z-index: 2;
                            @include title(s64);
                            font-family: 'LINOTTE-BOLD', sans-serif;
                        }
                        &-decs{   
                            word-wrap: break-word;  
                            @include title(s42);
                            font-family: 'LINOTTE-SEMI-BOLD', sans-serif;
                        }
                        &:hover{
                            background-color: map-get($color, bg-2 );
                            .items-name{
                                .title-color-bottom{
                                    &::after{
                                        background-color: map-get($color, text4 );
                                        width: 60%;
                                    }
                                }
                                
                            }
                            &>*{
                                // color: map-get($color,bg-3 );
                                
                            }
                        }
                    }
                    
                }
            }
        }
    }
}