#dp-education{
    background-color: map-get($color, bg-2 );
    @include section(s70, padding-bottom);
    @include section(s70, padding-top);

    .dp-education{
        width: 100%;
        &__wrapper{
            width: 100%;
            height: 100%;
            align-items: center;
            &--title{
                @include section(s70, margin-bottom);
                color: map-get($color, text7 );
            }
            &--list{
                width: 100%;
                position: relative;
                display: flex;
                align-items: center;
                padding-left: 40px;
                &-items{
                    .items-education{
                        @include title(s42);
                        font-family: 'LINOTTE-SEMI-BOLD', sans-serif;
                        color: map-get($color, text1 );
                        
                        &:not(:last-child){
                            margin-bottom: 50px;
                        }
                        &__decs{
                            @include title(s32);
                            color: map-get($color, text3 );
                            margin-top: 10px;
                        }
                    }
                    
                }
                &-img{
                    display: flex;
                    justify-content: center;
                    height: 100%;
                    position: relative;
                    @include rp(tl){
                        display: none;
                    }
                    .img-wrapper{
                        height: 100%;
                        position: relative;
                        position: relative;
                        z-index: 2;
                        img{
                           
                            object-fit: contain;
                        }
                       
                    }
                    .img-acsence{
                        transition: .9 linear;
                        width: 15vw;
                        height: 15vw;
                        max-width:300px ;
                        max-height:300px ;
                        border-radius: 50%;
                        background-color: white;
                        z-index: 1;
                    }
                }
            }
           
           
        }
    }
}