.white{
    #header{
        background-color: map-get($color, bg );
        .header-wrapper__logo{
            max-width: 80px;
            flex: unset;
            // padding: 10px;
            @include rp(tm){
                max-width: 60px;
            }
            svg{
                path{
                    fill: map-get($color, text2 )
                }
                circle{
                    fill: map-get($color, bg )!important
                }
                *{
                    fill: map-get($color, text2 ) !important
                }
            }
        }
        .header-wrapper__nav{
            &-title{
                color: map-get($color, text2 );
                svg{
                    margin-left: 10px;
                    width: 15px;
                    height: 15px;
                    transform: rotate(90deg);
                    path{
                        stroke: map-get($color, text2 )
                    }
                }
            }
            &-hamburger{
                .line-1{
                    background-color: map-get($color, text2 );
                    height: 6px;
                    width: 60px;
                    @include rp(tm){
                        width: 40px;
                    }
                }
                .line-2{
                    background-color: map-get($color, text2 );
                    height: 6px;
                    width: 40px;
                    @include rp(tm){
                        width: 30px;
                    }
                }
                .line-3{
                    background-color: map-get($color, text2 );
                    height: 6px;
                    width: 20px;
                    @include rp(tm){
                        width: 20px;
                    }
                }
                
            }
            
        }
    }
    .banner{
        background-color:black ;
        .banner-section__decs{
            &--infor{
                &_title{
                    color: map-get($color, text2 );
                }
                &_decs{
                    color: map-get($color, text2 );
                }
            }
        }
        .banner-section__img{
            &::after{
                background-color: map-get($color,text2 );
            }
        }
        &.bannerSub{
            // background-color: rgba($color: #000000, $alpha: .7);
            .banner-section__decs{
              
              &--infor{
                &_title{
                  @include title(s84);
                }
              }
             
            }
          }
    }
    #dp-information{
        background-color: black;
        .information{
            &__wrapper{
                width: 100%;
                &--title{
                    color: map-get($color, text2 );
                }
              
            }
        }
    }
}