.title-header{
    &>*{
        @include title(s101);
        font-family: 'LINOTTE-BOLD', sans-serif;
    }
}
.title-color-bottom{
    display: inline-block;
    p{
        position: relative;
        display: inline-block;
        z-index: 2;
        &::after{
            transition: .3s ease-in-out;
            position: absolute;
            bottom: -5px;
            left: 0;
            width: 80%;
            content: "";
            height: 35px;
            background-color: map-get($color, bg-2 );
            z-index: -1;
        }
    }
    
}