#dp-project{
    width: 100%;
    height: auto;
    background-color: black;
    transition: .3s ease-in-out;
    .project{
        @include section(s70, padding-bottom);
        @include section(s70, padding-top);
        &__wrapper{
            width: 100%;
            &--title{
                color: map-get($color, text2 );
            }
            &--list{
                @include section(s70, padding-top);
                display: flex;
                flex-wrap: wrap;
                margin: 0 -8px;
                // @include rp(tx){
                //     margin: 0 ;
                // }
                &_items{
                    padding: 8px;
                    
                    .items{
                        background-color: white;
                        height: 100%;
                        transition: .3s ease-in-out;
                        cursor: pointer;
                        padding-bottom: 60%;
                        display: flex;
                        flex-direction: column;
                        position: relative;
                        border-radius: 20px;
                        overflow: hidden;
                        @include rp(mx){
                            border-radius: 10px;
                        }
                        .items_link{
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            bottom: 0;
                            z-index: 2;
                        }
                        &-img{
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            bottom: 0;

                        }
                        &-hover{
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            background-color: rgba( map-get($color, bg ), .8);
                            padding: 50px;
                            display: flex;
                            flex-direction: column;
                            z-index: 1;
                            opacity: 0;
                            transition: .4s ease-in-out;
                            @include rp(pl){
                                padding: 2vw;
                            }
                            @include rp(mx){
                                padding: 3vw;
                            }
                            &::after{
                                position: absolute;
                                top: 50%;
                                left: 50px;
                                transform: translateY(-50%);
                                content: "";
                                height: 6px;
                                width: 40%;
                                background-color: map-get($color, bg-2 );
                                border-radius: 10px;
                                @include rp(pl){
                                    left: 2vw;
                                }
                                @include rp(mx){
                                    left: 3vw;
                                }
                            }
                            &>*{
                                flex: 1 1;
                            }
                            &_name{
                                .title-color-bottom{
                                    width: 100%;
                                    background-color: white;
                                    border-radius: 20px;
                                    padding: 30px;
                                    @include rp(pl){
                                        padding: 2vw;
                                    }
                                    @include rp(tm){
                                        // border-radius: 10px;
                                        padding: 4vw;
                                    }
                                    @include rp(mx){
                                        border-radius: 10px;
                                    }
                                    @include title(s54);
                                    font-family: 'LINOTTE-BOLD', sans-serif;
                                }
                               
                            }
                            &_decs{
                                padding-top: 50px;
                                @include rp(pl){
                                    padding-top: 2vw;
                                }
                                @include title(s32);
                                    font-family: 'LINOTTE-REGULAR', sans-serif;
                                    color: map-get($color, text2 );
                            }
                        }
                        &:hover{
                            .items-hover{
                                opacity: 1;
                            }
                        }
                        &::after{
                            position: absolute;
                            bottom: 0;
                            right: 0;
                            width: 93px;
                            height: 93px;
                            content: "";
                            background-color: map-get($color,bg-3 );
                            border-radius: 20px;
                            background-image: url("../assets/_page/exp/link.png");
                            background-position: center;
                            background-repeat: no-repeat;
                            background-size: 50%;
                            z-index: 3;
                            @include rp(pl){
                                width: 8vw;
                                height: 8vw;
                                // border-radius: 10px;
                            }
                            @include rp(mx){
                                width: 12vw;
                                height: 12vw;
                                border-radius: 10px;
                            }
                        }
                    }
                    
                }
            }
        }
    }
}