#dp-skills{
    background-color: #000000;
    @include section(s70, padding-bottom);
    @include section(s70, padding-top);
    .dp-skills{
        width: 100%;
        &__wrapper{
            width: 100%;
            height: 100%;
            align-items: center;
            &--title{
                @include section(s70, margin-bottom);
                color: map-get($color, bg-2 );
            }
            &--list{
                width: 100%;
                position: relative;
                display: flex;
                align-items: center;
                &-items{
                    width: 100%;
                    
                    .items-skills{
                        list-style-type: none;
                        cursor: pointer;
                        padding-left: 50px;
                        &:first-child{
                            position: relative;
                            @include title(s54);
                            .items-skills__items-skills{
                                text-align: end;
                                @include rp(tl){
                                    display: none;
                                }
                            }
                            .btn{
                                position: absolute;
                                left:0 ;
                                top: 22px;
                                // transform: translateY(-50%);
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                align-items: flex-end;
                                margin-left: auto;
                                transition: .3s ease-in-out;
                                .line-1{
                                    width: 32px;
                                    height: 6px;
                                    background-color: map-get($color,bg-3 );
                                    border-radius: 10px;
                                    margin-bottom: 2px;
                                    transition: .3s ease-in-out;
                                    
                                }
                                .line-2{
                                    width: 21px;
                                    height: 6px;
                                    background-color: map-get($color,bg-3 );
                                    border-radius: 10px;
                                    transition: .3s ease-in-out;
                                    transform: rotate(0);
                                }
                            }
                        }
                        &:not(:last-child){
                            margin-bottom: 30px;
                            @include rp(tl){
                                margin-bottom: 35px;
                            }
                        }
                        &__items{
                            display: flex;
                            color: map-get($color, text2 );
                            font-family: 'LINOTTE-SEMI-BOLD', sans-serif;
                            @include rp(tl){
                                flex-direction: column;
                            }
                            &-title{

                            }
                            &-skills{
                                display: flex;
                                justify-content: flex-end;
                                align-items: center;
                                @include rp(tl){
                                    justify-content: flex-start;
                                    margin-top: 15px;
                                }
                                &>*:not(:first-child){
                                    margin-left: 15px;
                                }
                                span{
                                    transition: .3s ease-in-out;
                                    
                                    width: 30px;
                                    height: 30px;
                                    @include rp(tl){
                                        width: 25px;
                                        height: 25px;
                                    }
                                    border-radius: 50%;
                                    border: solid 1px black;
                                    background-color: white;
                                    &.yes{
                                        border: unset;
                                        background-color: map-get($color,bg-3 );
                                    }
                                    
                                }
                                
                            }
                            &:hover{
                                .items-skills__items-skills{
                                    &>*:not(:first-child){
                                        margin-left: 25px;
                                    }
                                }   
                            }
                        }
                    }
                   
                }
                
            }
           
           
        }
    }
}