#dp-progress{
    .progress{
        @include section(s70, padding-bottom);
        @include section(s70, padding-top);
        &__wrapper{
            width: 100%;
            &--list{
                &-items{
                    .items{
                        list-style-type: none;
                        &-name{
                            @include title(s42);
                            font-family: 'LINOTTE-SEMI-BOLD', sans-serif;
                            margin-bottom: 50px;
                            @include section(s150, margin-top);
                            color:  map-get($color, bg );
                            position: relative;
                            @include rp(mx){
                                margin-bottom: 20px;
                            }
                            &::after{
                                content: "";
                                height: 130px;
                                width: 130px;
                                background-color: map-get($color, bg-2 );
                                position: absolute;
                                left: -40px;
                                border-radius: 50%;
                                top: -70px;
                                z-index: -1;
                                @include rp(mx){
                                    height: 80px;
                                    width: 80px;
                                    left: 0;
                                    top: -40px;
                                }
                            }
                        }
                        &-infor{
                            &__items{
                                display: flex;
                                flex-wrap: wrap;
                                &-year{
                                    margin-bottom: 30px;
                                    @include rp(mx){
                                        margin-bottom: 20px;
                                    }
                                    padding-inline-start: 40px;
                                    position: relative;
                                    color:  map-get($color, text3 );
                                    font-family: 'LINOTTE-BOLD', sans-serif;
                                    @include title(s32);
                                    &::before{
                                        content: "";
                                        height: 6px;
                                        width: 25px;
                                        background-color: map-get($color, text3 );
                                        position: absolute;
                                        left: 0;
                                        border-radius: 10px;
                                        top: 18px;
                                    }
                                }
                                &-progresss{
                                    &_infor{
                                        font-family: 'LINOTTE-SEMI-BOLD', sans-serif;
                                        @include title(s42);
                                        margin-bottom: 30px;
                                        @include rp(mx){
                                            margin-bottom: 20px;
                                        }
                                    }
                                    &_decs{
                                        font-family: 'LINOTTE-REGULAR', sans-serif;
                                        @include title(s32);
                                        color:  map-get($color, text3 );
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}