#dp-soft-skill{
    background-color: white;
    @include section(s70, padding-bottom);
    @include section(s70, padding-top);
    .soft-skill{
        width: 100%;
        &__wrapper{
            width: 100%;
            height: 100%;
            align-items: center;
            &--title{
                @include section(s70, margin-bottom);
            }
            &--list{
                width: 100%;
                position: relative;
                display: flex;
                align-items: center;
                transition: .3s ease-in-out;
                &-items{
                    ol {
                        padding-inline-start: 40px;
                        @include title(s42);
                        color: map-get($color, text6 );
                        font-family: 'LINOTTE-SEMI-BOLD', sans-serif;
                        li{
                            .items{
                                position: relative;
                                z-index: 1;
                               &-name{
                                    cursor: pointer;
                                    padding: 25px 0;
                                    display: flex;
                                    align-items: center;
                                    .btn{
                                        display: flex;
                                        flex-direction: column;
                                        justify-content: center;
                                        align-items: flex-end;
                                        margin-left: auto;
                                        transition: .3s ease-in-out;
                                        .line-1{
                                            width: 32px;
                                            height: 6px;
                                            background-color: map-get($color,bg-3 );
                                            border-radius: 10px;
                                            margin-bottom: 2px;
                                            transition: .3s ease-in-out;
                                            
                                        }
                                        .line-2{
                                            width: 21px;
                                            height: 6px;
                                            background-color: map-get($color,bg-3 );
                                            border-radius: 10px;
                                            transition: .3s ease-in-out;
                                            transform: rotate(0);
                                        }
                                    }
                                    position: relative;
                                
                                    &::after{
                                        position: absolute;
                                        top:  100%;
                                        left: -40px;
                                        width: calc(100% + 40px);
                                        height: 1px;
                                        background-color: map-get($color,text3 );
                                        content: "";
                                    }
                                }
                                &.active{
                                    .items-name .btn{
                                        transform: translate(10px, 0);
                                        .line-1{
                                            transform: rotate(45deg) translateX(1px) translateY(12px);
                                            transition-delay: .3s ;
                                        }
                                        .line-2{
                                            animation: rotate .6s ease-in-out forwards;
                                            // transform: rotate(135deg);
                                            @keyframes rotate {
                                                0%{
                                                    width: 21px;
                                                }
                                                50%{
                                                    width: 32px;
                                                    transform: rotate(0);
                                                }
                                                100%{
                                                    width: 32px;
                                                    transform: rotate(135deg) translateX(7px) translateY(5px);
                                                }
                                            }
                                        }
                                    }
                                    .items-name span{
                                        width: 18px;
                                        height: 6px;
                                        border-radius: 10px;
                                        // background: map-get($color, main-second );
                                        animation: rotate .5s ease-in-out;
                                        
                                    }
                                    .items-infor{
                                        padding-left: 40px;
                                        transform: scaleY(1);
                                        height: 100%;
                                        padding: 30px 0;
                                        &::-webkit-scrollbar {
                                            // display: none;
                                        }
                                    }
                                }
                                &-infor{
                                    font-family: 'LINOTTE-REGULAR', sans-serif;
                                    padding-left: -40px;
                                    padding: 0;
                                    @include title(s32);
                                    color: map-get($color, black-1 );
                                    position: relative;
                                    overflow: hidden;
                                    margin-left: -40px;
                                    transition: .5s ease-in-out;
                                    height: 0px;
                                    transform: scaleY(0);    
                                    transform-origin: top;
                                    transition: transform 0.26s ease-in-out;
                                    position: relative;
                                    z-index: 2;
                                    &__items{
                                        display: flex;
                                        align-items: center;
                                        cursor: pointer;
                                        &:not(:last-child){
                                            margin-bottom: 15px;
                                        }
                                        &-title{
                                            
                                        }
                                        &-skills{
                                            display: flex;
                                            justify-content: flex-end;
                                            &>*{
                                                transition: .3s ease-in-out;
                                                margin-left: 15px;
                                                width:22px;
                                                height: 22px;
                                                border-radius: 50%;
                                                border: solid 1px black;
                                                background-color: white;
                                                &.yes{
                                                    border: unset;
                                                    background-color: map-get($color,bg-3 );
                                                }
                                            }
                                        }
                                        &:hover{
                                            .items-infor__items-skills{
                                                &>*{
                                                    margin-left: 25px;
                                                }
                                            }   
                                        }
                                    }
                                    &.active{
                                        
                                    }
                                    
                                    &::after{
                                        position: absolute;
                                        top:  99%;
                                        left: -40px;
                                        width: calc(100% + 40px);
                                        height: 1px;
                                        background-color: map-get($color,text3 );
                                        content: "";
                                    }
                                }
                            }
                        }
                    }
                }
                &-img{
                    display: flex;
                    justify-content: center;
                    height: 100%;
                    position: relative;
                    transition: .3s ease-in-out;
                    @include rp(ts){
                        display: none;
                    }
                    .img-wrapper{
                        height: 100%;
                        position: relative;
                        img{
                            object-fit: contain;
                        }
                    }
                    .img-acsence{
                        transition: .9 linear;
                        // transition-duration: .9s linear;
                        // position: absolute;
                        // top: 50px;
                        // right: 50px;
                        width: 17vw;
                        height: 17vw;
                        min-height: 200px;
                        min-width: 200px;
                        max-width:400px ;
                        max-height:400px ;
                        border-radius: 30%;
                        background-color: rgba( map-get($color , text3 ),.16);
                        
                    }
                }
            }
           
           
        }
    }
}