
.banner{
    height: 100vh;
    max-height: 1080px;
    width: 100%;
    position: relative;
    overflow: hidden;
    transition: .3s ease-in-out;
    background-color: map-get($color, text2 );
    &.bannerSub{
      .banner-section__decs{
        
        &--infor{
          &_title{
            @include title(s84);
          }
          &_decs{
            @include title(s54);
          }
        }
       
      }
      
    }
    .banner-section{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      height: auto;
      // overflow: hidden;
      @include rp(ts){
        position: unset;
        transform:unset;
        height: 100%;
      }
      .wrapper{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        // margin: 0 -8px;
        // justify-content: space-between;
        // &>*{
        //   flex: 1 1;
        // }
        align-items: center;
        @include rp(tl){
          justify-content: center;
        }
      }
      &__img{
        padding: 0 8px;
        position: relative;
        min-width: 300px;
        animation: Opacity 1s ease-in-out;
        animation-fill-mode: both;
        opacity: 0;
        @keyframes Opacity {
          from{
            // transform: translateY(100px);
            opacity: 0;
          }
          to{
            // transform: translateY(0);
            opacity: 1;
          }
        }
        // @include rp(ts){
        //   margin-top: ;
        // }
        &::after{
          transition: .3s ease-in-out;
          position: absolute;
          top: 0;
          left: 0;
          // transform: translate(-50%,-50%);
          width: 100%;
          height: 100%;
          content: "";
          background-color: map-get($color,bg );
          border-radius: 50%;
          animation: flow  5s infinite linear;
          z-index: 1;
          box-shadow: 0px 0px 20px map-get($color, bg );
          @keyframes flow {
            0%{
              transform:rotate(0deg)
                        translate(-15px)
                        // translateY(50%)
                        rotate(0deg);
            }
            100%{
              transform:rotate(360deg)
                          translate(-15px)
                        //  translateY(50%)
                        rotate(-360deg);
            }
          }
        }
        // z-index: 2;
        .img{
          width: 100%;
          padding-top: 100%;
          // background-color: black;
          border-radius: 50%;
          background: transparent;
          overflow: hidden;
          position: relative;
          z-index: 2;
          img{
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            z-index: 1;
          }
        
        }
       
      }
      &__decs{
        margin-right: auto;
        padding: 0 8px;
        &--infor{
          &_title{
            text{
             
            }
            @include title(s101);
            @include p-line-clamp(3);
            margin-bottom: 20px;
            color: map-get($color, text1 );
            font-family: 'LINOTTE-BOLD', sans-serif;
            transition: .3s ease-in-out;
            animation: fadeUp 1s ease-in-out;
            // transform: translateY(0);
            animation-fill-mode: both;
            opacity: 0;
          }
          &_decs{
            transition: .3s ease-in-out;
            @include title(s64);
            @include p-line-clamp(8);
            font-family: 'LINOTTE-BOLD', sans-serif;
            color: map-get($color, text3 );
            animation: fadeUp 1s ease-in-out;
            animation-fill-mode: both;
            animation-delay: .7s;
            opacity: 0;
          }
        }
        @keyframes fadeUp {
          from{
            transform: translateY(100px);
            // opacity: 0;
          }
          to{
            transform: translateY(0);
            opacity: 1;
          }
        }
      }
    }
  }