#footer{
    .footer{
        &-wrapper{
            display: flex;
            align-items: center;
            padding: 30px 0;
            flex-wrap: wrap;
            @include rp(tl){
               justify-content: center;
               padding: 20px 0;
            }
            &__contact{
                display: flex;
                align-items: center;
                @include title(s32);
                font-family: 'LINOTTE-BOLD', sans-serif;
                @include rp(tl){
                   margin-bottom: 15px;
                }
                .social__items{
                    &>*{
                        margin: 0 10px;
                        @include rp(mx){
                            margin: 0 5px;
                        }
                        &:hover{
                            svg{
                                path{
                                    fill: map-get($color, text7 ) ;
                                }
                            }
                        }
                    }
                    svg{
                        path{
                            transition: .3s ease-in-out;
                        }
                        width: 30px;
                        height: 30px;
                        @include rp(mx){
                            width: 20px;
                            height: 20px;
                        }
                    }
                }
            }
            &__copyright{
                margin-left: auto;
                @include title(s32);
                font-family: 'LINOTTE-REGULAR', sans-serif;
                
                @include rp(tl){
                    margin-left: unset;
                    text-align: center;
                }
            }
        }
    }
}